.features {
    min-width: 320px;
    padding: 48px 25px 0;
    background: $text-white;

    @media (min-width: $tablet-width) {
        min-width: 768px;
        padding: 80px 50px 75px;
    }

    @media (min-width: $desktop-width) {
        padding: 120px 20px 120px;
    }
}

.features__list {
    @include reset;
    list-style-type: none;
    display: grid;

    @media (min-width: $tablet-width) {
        grid-template-columns: 1fr 1fr;
        grid-gap: 30px;
    }

    @media (min-width: $desktop-width) {
        min-width: 984px;
        max-width: 1220px;
        margin: 0 auto;
    }
}

.features__item {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 30px;
    min-height: 340px;

    &:hover {
        background: $main-blue;
    }

    &:hover .features__item-header,
    &:hover .features__item-text {
        color: $text-white;
    }

    &:hover .features__item-image-path {
        fill: $text-white;
    }

    @media (min-width: $tablet-width) {
        border: 1px solid #ebebeb;
        padding: 38px 36px 12px 44px;
        min-height: 360px;
    }

    @media (min-width: $desktop-width) {
        padding: 38px 42px 35px;
        // width: auto;
        max-width: 595px;
        min-height: 300px;
    }
}

.features__item-image {
    @media (min-width: $tablet-width) {
        width: 35px;
    }
}

.features__item--third,
.features__item--fourth {
    min-height: 325px;

    @media (min-width: $tablet-width) {
        min-height: 360px;
    }
}

.features__item--fourth .features__item-header {
    @media (min-width: $tablet-width) {
        margin: 12px 0 3px;
    }

    @media (min-width: $desktop-width) {
        margin: 22px 0 12px;
    }
}

.features__item--third .features__item-header {
    @media (min-width: $tablet-width) {
        margin: 16px 0 6px;
    }

    @media (min-width: $desktop-width) {
        margin: 22px 0 12px;
    }
}

.features__item--active {
    background: $main-blue;

    .features__item-header {
        color: $text-white;
    }

    .features__item-text {
        color: $text-white;
    }
}

.features__blue-decor {
    position: absolute;
    height: 100%;
    width: 5px;
    background: $main-blue;
    left: 0;
    top: 0;
}

.features__item:not(:last-of-type) {
    margin-bottom: 30px;

    @media (min-width: $tablet-width) {
        margin-bottom: 0;
    }
}

.features__item-header {
    margin: 12px 0 10px;
    @include header-font-regular;
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 0;
    text-align: left;
    color: $main-blue;

    @media (min-width: $tablet-width) {
        margin: 16px 0 10px;
        font-size: 24px;
        line-height: 34px;
    }

    @media (min-width: $desktop-width) {
        margin: 22px 0 12px;
    }
}

.features__item-header--third,
.features__item-header--fourth {
    margin: 15px 0 12px;
}

.features__item--fourth {
    padding-right: 40px;
}

.features__item-text {
    margin: 0 0 15px;
    @include common-font-regular;
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0;
    text-align: left;
    text-align: left;
    color: $text-color;

    @media (min-width: $tablet-width) {
        font-size: 16px;
        line-height: 22px;
    }
}

.features__text--third,
.features__text--fourth {
    margin: 7px 0 0;
}
