.socials {
    margin: 20px 0 28px;
    padding: 0 92px;

    @media (min-width: $tablet-width) {
        margin: 10px -2px 28px 0;
        grid-column: 2/3;
        padding: 0;
        justify-self: end;
    }

    @media (min-width: $desktop-width) {
        grid-column: 1/2;
        grid-row: 2/3;
        justify-self: start;
        margin: 50px 0 0 15px;
    }
}

.socials__list {
    @include reset;
    display: flex;
    justify-content: space-between;
    list-style-type: none;
    width: 140px;
}

.socials__link--pinterest {
    display: grid;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    border-radius: 15px;
    background: $text-white;
}