.footer-menu {
    min-width: 320px;
    
    @media (min-width: $tablet-width) {
        margin: 5px 0 0;
    }
}

.footer-menu__header {
    @include header-font-regular;
    font-size: 15px;
    line-height: 16px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    text-align: left;
    color: $text-white;
    display: block;
    padding: 22px 25px;

    @media (min-width: $tablet-width) {
        padding: 20px 0;
    }

    @media (min-width: $desktop-width) {
        padding: 0;
    }
}

.footer-menu::before {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background: rgba(248, 253, 255, 0.7);

    @media (min-width: $tablet-width) {
        display: none;
    }
}

.footer-menu::after {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background: rgba(248, 253, 255, 0.7);

    @media (min-width: $tablet-width) {
        display: none;
    }
}

.footer-menu__wrapper {
    display: flex;
    justify-content: space-between;
}

.footer-menu__accordeon-button {
    padding: 0 25px 0 0;
    align-self: center;

    @media (min-width: $tablet-width) {
        display: none;
    }
}

.footer-menu__list {
    @include reset;
    list-style-type: none;
    margin: 0 25px 20px;

    @media (min-width: $tablet-width) {
        height: 240px;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        margin: 0;
        width: 220px;
    }

    @media (min-width: $desktop-width) {
        height: 160px;
        width: 248px;
        margin: 23px 0 0;
    }
}

.footer-menu__item {
    @include common-font-regular;
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0;
    text-align: left;
    color: rgba(255, 255, 255, 0.7);

    @media (min-width: $tablet-width) {
        font-size: 16px;
        line-height: 22px;
    }
}

.footer-menu__item:not(:last-of-type) {
    margin-bottom: 8px;
}

.footer-menu__no-display {
    display: none;
}

.footer-menu__display {
    display: block;
}

.footer-menu__list--no-js {
    display: block;
}