.footer-logo {
    display: grid;
    grid-template-columns: 70px 1fr;
    padding: 0 50px;
    
    @media (min-width: $tablet-width) {
        padding: 0;
    }

    @media (min-width: $desktop-width) {
        grid-column: 1/2;
        grid-row: 1/2;
    }
}

.footer-logo__img {
    display: block;
    grid-column: 1/2;
    grid-row: 1/3;
}

.footer-logo__logo-name {
    margin: 5px 0 0;
    align-items: end;
    @include header-font-bold;
    font-size: 25px;
    line-height: 29px;
    letter-spacing: 0;
    text-align: left;
    color: $text-white;
}

.footer-logo__logo-text {
    margin: -5px 0 0;
    font-family: "Roboto", "Arial", sans-serif;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0;
    text-align: left;    
    color: rgba(248, 253, 255, 0.7);

    @media (min-width: $tablet-width) {
        margin: -15px 0 0;
    }

    @media (min-width: $desktop-width) {
        margin: -8px 0 0;
    }
}