.footer__contacts {

    @media (min-width: $desktop-width) {
        grid-column: 3/4;
        grid-row: 1/7;

    }
}

.footer-contacts__header {
    @include header-font-regular;
    font-size: 15px;
    line-height: 16px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    text-align: left;
    color: $text-white;
    display: block;
    padding: 20px 25px;

    @media (min-width: $desktop-width) {
        padding: 0 25px;
    }
}

.footer-contacts__wrapper {
    display: flex;
    justify-content: space-between;

    @media (min-width: $tablet-width) {
        margin: 5px 0 0 -10px;
    }
}

.footer-contacts__accordeon-button {
    padding: 0 25px 0 0;
    align-self: center;

    @media (min-width: $tablet-width) {
        display: none;
    }
}

// .footer-contacts__accordeon-minus {
//     display: none;
// }

.footer-contacts__list {
    @include reset;
    list-style-type: none;
    margin: 0 25px 12px;
    // display: none;

    @media (min-width: $tablet-width) {
        width: 310px;
        display: block;
        margin: 0 0 12px 16px;
    }

    @media (min-width: $desktop-width) {
        margin: 23px 0 0 16px;
    }
}

.footer-contacts__item {
    @include common-font-regular;
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0;
    text-align: left;
    color: rgba(255, 255, 255, 0.7);

    @media (min-width: $tablet-width) {
        font-size: 16px;
        line-height: 22px;
    }
}

.footer-contacts__item:not(:last-of-type) {
    margin-bottom: 15px;

    @media (min-width: $tablet-width) {
        margin-bottom: 8px;
    }
}

.footer-contacts::after {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background: rgba(248, 253, 255, 0.7);

    @media (min-width: $tablet-width) {
        display: none;
    }
}

.footer-contacts__no-display {
    display: none;
}

.footer-contacts__display {
    display: block;
}

.footer-contacts__list--no-js {
    display: block;
}