.about {
    min-width: 320px;
    padding: 50px 25px 80px;
    background: $text-white;

    @media (min-width: $tablet-width) {
        padding: 0 0 80px;
    }

    @media (min-width: $desktop-width) {
        padding: 0 20px;
    }
}

.about__container-wrapper {
    @media (min-width: $desktop-width) {
        min-width: 984px;
        max-width: 1220px;
        margin: 0 auto;
    }
}

.about__container {
    @media (min-width: $tablet-width) {
        padding: 0 47px;
        display: grid;
        grid-template-columns: 178px 1fr;
    }

    @media (min-width: $desktop-width) {
        grid-template-columns: 250px 1fr;
        grid-auto-rows: minmax(15px, auto);
        padding: 0 0 100px;
        min-width: 984px;
    }
}

.about__img {
    display: none;

    @media (min-width: $tablet-width) {
        margin: 5px 0 0;
        display: block;
        grid-column: 1/2;
        grid-row: 1/3;
    }

    @media (min-width: $desktop-width) {
        margin: 0;
    }
}

.about__text-container {
    @media (min-width: $desktop-width) {
        display: flex;
        flex-direction: column;
        // width: 950px;
    }
}

.about__header {
    @include header-font-regular;
    font-size: 28px;
    line-height: 39px;
    letter-spacing: 0;
    text-align: left;
    color: $text-color;

    @media (min-width: $tablet-width) {
        font-size: 36px;
        line-height: 51px;
    }
}

.about__text {
    @include common-font-regular;
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0;
    text-align: left;
    color: $text-color;
    margin: 10px 0 16px;

    @media (min-width: $tablet-width) {
        margin: 10px 0 0;
        font-size: 16px;
        line-height: 22px;
    }
}

.about__text--second-paragraph {
    @media (min-width: $tablet-width) {
        margin: 20px 0 0;
    }
}


.about__text--after-divider,
.about__text--after-divider-mobile {
    display: none;
    @include common-font-regular;
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0;
    text-align: left;
    color: $text-color;
    margin: 10px 0 20px;

    // @media (min-width: $desktop-width) {
    //     display: none;
    // }
}

.about__text--after-divider-mobile {
    @media (min-width: $tablet-width) {
        display: inline;
    }
}

.about__button {
    min-height: 35px;
    width: 160px;
    background: $text-white;
    border-radius: 3px;
    border: 1px solid $main-blue;
    @include header-font-bold;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.05em;
    text-align: center;
    text-transform: uppercase;
    color: $main-blue;
    box-sizing: border-box;

    @media (min-width: $tablet-width) {
        height: 45px;
        width: 180px;
        line-height: 17px;
        margin: 18px 0 0;
    }
}

.about__click {
    opacity: 1;
}
