.feedback-form {
    @media (min-width: $tablet-width) {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    @media (min-width: $desktop-width) {
        width: 100%;
        max-width: 590px;
        padding: 0 10px 0 0;
    }
}

.feedback-form__header {
    display: block;
    @include header-font-regular;
    font-size: 30px;
    line-height: 42px;
    letter-spacing: 0;
    text-align: left;
    color: $text-white;
    margin: 0 0 6px;

    @media (min-width: $tablet-width) {
        font-size: 36px;
        line-height: 51px;
        margin: 0 0 7px;
        grid-column: 1/3;
    }

    @media (min-width: $desktop-width) {
        margin: 0 0 10px -5px;
    }
}

.feedback-form__secondary-header {
    @include common-font-regular;
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0;
    text-align: left;    
    color: $text-white;
    margin: 0 0 35px;

    @media (min-width: $tablet-width) {
        font-size: 16px;
        line-height: 22px;
        margin: 0 0 25px;
        grid-column: 1/3;
    }

    @media (min-width: $desktop-width) {
        margin: 0 0 35px;
    }
}

.feedback-form__input {
    display: block;
    height: 50px;
    width: 100%;
    border-radius: 3px;
    background: rgba(4, 20, 39, 0.15);
    border: 0;
    outline: none;
    margin-bottom: 20px;
    box-sizing: border-box;
    padding: 15px 13px;
    @include common-font-regular;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 0;
    text-align: left;
    color: $text-white;

    &::placeholder {
        @include common-font-regular;
        font-size: 16px;
        line-height: 22px;
        letter-spacing: 0;
        text-align: left;    
        color: $text-white;
    }

    @media (min-width: $tablet-width) {
        width: 320px;
    }

    @media (min-width: $desktop-width) {
        // width: 100%;
        max-width: 275px;
    }
}

.feedback-form__name {
    @media (min-width: $tablet-width) {
        grid-column: 1/2;
    }

    @media (min-width: $desktop-width) {
        // margin-right: 20px;
    }
}

.feedback-form__phone {
    @media (min-width: $tablet-width) {
        grid-column: 2/3;
        justify-self: end;
    }

    @media (min-width: $desktop-width) {
        // margin-left: 20px;
    }
}

.feedback-form__question {
    height: 180px;
    width: 100%;
    border-radius: 3px;
    background: rgba(4, 20, 39, 0.15);
    border: 0;
    outline: none;
    box-sizing: border-box;
    margin-bottom: 23px;
    padding: 20px 12px;
    @include common-font-regular;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 0;
    text-align: left;
    color: $text-white;

    &::placeholder {
        @include common-font-regular;
        font-size: 16px;
        line-height: 22px;
        letter-spacing: 0;
        text-align: left;    
        color: $text-white;
    }

    @media (min-width: $tablet-width) {
        height: 155px;
        margin-bottom: 35px;
        grid-column: 1/3;
        padding: 25px 12px;
    }

    @media (min-width: $desktop-width) {
        width: 100%;
    }
}

.feedback-form__button {
    height: 45px;
    width: 100%;
    border-radius: 3px;
    background: $black-btn;
    border: 0;
    @include header-font-bold;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 0.05em;
    text-align: center;
    text-transform: uppercase;
    color: $text-white;

    @media (min-width: $tablet-width) {
        grid-column: 1/2;
        width: 265px;
    }
}

.feedback-form__agreement-wrapper {
    display: flex;
    flex-direction: row-reverse;
    position: relative;
    margin: 25px 5px 0;

    @media (min-width: $tablet-width) {
        grid-column: 2/3;
        margin: 0;
        padding-left: 16px;
    }

    @media (min-width: $desktop-width) {
        margin: 15px 0 0 10px;
    }
}

.feedback-form__checkbox {
    visibility: hidden;
}

.feedback-form__label {
    @include common-font-regular;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0;
    text-align: left;
    color: $text-white;
    margin: 0 0 0 12px;
    align-self: center;
}

.feedback-form__checkbox-container::before {
    content: "";
    display: block;
    position: absolute;
    box-sizing: border-box;
    border-radius: 3px;
    top: 50%;
    transform: translate(0, -50%);
    background: transparent;
    width: 15px;
    height: 14px;
    border: 1px solid $text-white;
    justify-self: center;
}

.feedback-form__checkbox:checked ~ .feedback-form__checkmark::before {
    content: "";
    display: block;
    position: absolute;
    box-sizing: border-box;
    // border-radius: 3px;
    top: 50%;
    transform: translate(0, -50%);
    background: url(../img/svg/checkmark.svg) no-repeat 50% 50%;
    width: 15px;
    height: 14px;
    // border: 1px solid $text-white;
    justify-self: center;
}
